// rotates any 3darray with same width,height,depth
// clockwise = 1 = 90 degrees
// clockwise = 0 = -90 degrees array[y][z][x]
export function rotateTop(array, clockwise) {
  var newTop = [];

  for (var x = 0; x < array[0].length; x++) {
    var newRow = [];
    if (clockwise) for (var z = 0; z < array[0].length; z++) {
      var negZ = z - array.length - 1;
      negZ = negZ ? -1 * (negZ + 2) : negZ;
      newRow.push(array[0][negZ][x]);
    } else {
      for (var _z = 0; _z < array[0].length; _z++) {
        var negX = x - array.length - 1;
        negX = negX ? -1 * (negX + 2) : negX;
        newRow.push(array[0][_z][negX]);
      }
    }
    newTop.push(newRow);
  }

  array[0] = newTop;
} // rotateBottom
