export function rotateLeft(array, clockwise) {
  var arrayCopy = arrayShallowCopy(array);

  for (var y = 0; y < array.length; y++) {
    for (var z = 0; z < array.length; z++) {
      var negY = y - array.length - 1;
      negY = negY ? -1 * (negY + 2) : negY;
      var negZ = z - array.length - 1;
      negZ = negZ ? -1 * (negZ + 2) : negZ;
      if (clockwise) array[y][z][0] = arrayCopy[negZ][y][0];else array[y][z][0] = arrayCopy[z][negY][0];
    }
  }
} // rotateRight
